
header {
  flex:0.00001;
  display:flex;
  transition:flex 0.44s 0.44s;
}

.menu-open {
  transition-delay:0s;
}

.trigger {
  width:104px;
  height:104px;
  z-index:$top;
  position:absolute;
  right:0;
  &__container {
    width:50%;
    height:50%;
    position:relative;
    cursor:pointer;
  }
  &__line {
    display:block;
    height:2px;
    width:24px;
    position:absolute;
    top:25px;left:14px;
  }
  &--menu {
    background:$white;
    transition:background 1s linear $slow;
  }
  &__line {
    background:$black;
    transition:background $quick $cubic $middle,transform $middle $cubic $instant;
    &--top {
      transform:translateY(-4px);
    }
    &--bottom {
      transform:translateY(4px);
    }
  }
}

.menu {
  opacity:0;
  width:100%;
  text-align:center;
  visibility:hidden;
  transition:opacity $quick $cubic $middle, visibility $instant $slow;
}

.menu__list {
  height:60%;
  font-size:1.4em;
  overflow:hidden;
  flex-direction:column;
  @media only screen and (min-width:$small) {
    width:70%;
    flex-direction:row;
  }
}

.menu__item {
  flex:1;
  opacity:0;
  transition:opacity $quick ease-in;
  @for $i from 3 through 1 {
    &:nth-of-type(#{$i}) {
      transition-delay:(#{$i * 0.11}s);
    }
  }
  // &:nth-child(1) {
  //   transition-delay:$quick;
  // }
  // &:nth-child(2) {
  //   transition-delay:0.11s;
  // }
  // &:nth-child(3) {
  //   transition-delay:$instant;
  // }
  @media only screen and (min-width:$small) {
    height:100%;
  }
}

.menu__link {
  height:100%;
  display:inline-flex;
  flex-direction:column;
  justify-content:center;
  padding:2.5% 1em 0;
  line-height:1;
  &::before {
    content:'';
    display:block;
    margin-bottom:0.16em;
    font-size:2em;
    height:1em;
    background:center center / contain no-repeat;
  }
  &--landing::before {
    background-image:url('/assets/img/icons/map-signs.svg')
  }
  &--about::before {
    background-image:url('/assets/img/icons/soccer-ball.svg')
  }
  &--projects::before {
    background-image:url('/assets/img/icons/floppy-disk.svg')
  }
  &::after {
    content:'';
    display:block;
    width:0;height:2px;
    margin:0 auto;
    background:$black;
    opacity:0;
    transform:translateY(-3.2em);
    transition:width $slow,opacity $average,transform $average;
  }
  &:hover::after {
    width:100%;
    opacity:1;
    transform:translateY(0)
  }
}

.menu-open {
  flex:1;
  position:relative;

  .trigger--menu {
    background:transparent;
    transition:background $instant;
  }

  .trigger__line {
    transition-delay:$instant;
    &--top {
      transform:rotate(-45deg);
    }
    &--bottom {
      transform:rotate(45deg);
    }
  }

  .menu {
    opacity:1;
    visibility:visible;
    transition:opacity $quick $cubic $instant;
  }

  .menu__item {
    opacity:1;
    transition-duration:0.42s;
    @for $i from 1 through 3 {
      &:nth-of-type(#{$i}) {
        transition-delay:(#{($i * 0.11) + 0.44}s);
      }
    }
    // @for $i from 1 through 5 {
    //   &:nth-child(#{$i}) {
    //     &:before {
    //       content: '#{$i}';
    //     }
    //   }
    // }
    // &:nth-child(1) {
    //   transition-delay:$quick;
    // }
    // &:nth-child(2) {
    //   transition-delay:$average;
    // }
    // &:nth-child(3) {
    //   transition-delay:$middle;
    // }
  }
}

